import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Customtools } from '../../../http/customUtil';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { MessageService } from 'primeng/components/common/messageservice';

@Component({
  selector: 'app-content-modify',
  templateUrl: './content-modify.component.html',
  styleUrls: ['./content-modify.component.scss']
})
export class ContentModifyComponent implements OnInit {
  @Input() inValueItem: any;
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
  inputValue: any;  // 传入子组件数据
  data = []; // 列表
  isUpdate = true; // 是否允许删除问题
  loadingShow: any = { flag: false }; // loading框
  // papeType = papeType; // 问卷类型
  dialogDisplay = false;// 选择问题弹窗
  inValueList = []; // 传入子组件数据
  message: any = { msg: [] };
  constructor(
    private commonFunc: Customtools,
    private httpService:CusOperationHttpService,
    private messageService:MessageService
  ) { }

  ngOnInit() {
  }


  dataCall(event) {
    this.data = event;
  }


  // 新增问题
  add() {
    this.dialogDisplay = true;
    this.inValueList = this.data;
  }
  // 确认生成问卷
  save(isMessage?) {
    const params = {
      'submitCaption': this.inValueItem.submitCaption,
      'questionnaireId': this.inValueItem.surveyId,
      'questionList': this.data
    };
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: isMessage ? false : true,
      inputParam: params,
      message: this.messageService,
      injectName: 'generateRiskRateQuestionnaire',
      loadingShow: this.loadingShow,
      messageInfo: '保存成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.outValueEmit.emit(true);
      }
    });
  }

  // 取消
  cancle() {
    this.outValueEmit.emit(false);
  }
}
