import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Customtools } from '../../../http/customUtil';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { MessageService } from 'primeng/components/common/messageservice';

@Component({
  selector: 'app-content-add',
  templateUrl: './content-add.component.html',
  styleUrls: ['./content-add.component.scss'],
  providers:[MessageService]
})
export class ContentAddComponent implements OnInit {
  // @Input() inValueA: any;
  @Input() inValueItem: any;
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
  valUrl:any;
  public myAngularxQrCode: string = null;
  inputValue: any;  // 传入子组件数据
  data = []; // 列表
  isUpdate = true; // 是否允许删除问题
  loadingShow: any = { flag: false }; // loading框
  // papeType = papeType; // 问卷类型
  dialogDisplay = false;// 选择问题弹窗
  inValueList = []; // 传入子组件数据
  message: any = { msg: [] };
  detaildisplay:boolean = false;
  list:any = [];//存放选中的类型
  inValueDetail:any;//传递给预览页面的值
  constructor(
    private commonFunc: Customtools,
    private httpService:CusOperationHttpService,
    private messageService:MessageService
  ) {
    this.myAngularxQrCode = 'Your QR code data string';
   }

  ngOnInit() {
    this.valUrl = 'http://172.16.101.133/#/pages/tzb/custom/work-rask/credit-intergration'
  }
  dataCall (event) {
    this.data = event;
    console.log(this.data ,'处理后的data')
  }
  // 新增问题
  add() {
    this.dialogDisplay = true;
    this.inValueList = this.data;
  }
  // 确认生成问卷
  save(isMessage?) {
    const params = {
      'submitCaption': this.inValueItem.submitCaption,
      'questionnaireId': this.inValueItem.surveyId,
      'questionList': this.data
    };
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: isMessage ? false : true,
      inputParam: params,
      message: this.messageService,
      injectName: 'generateRiskRateQuestionnaire',
      loadingShow: this.loadingShow,
      messageInfo: '保存成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.outValueEmit.emit(true);
      }
    });
  }

  // 取消
  cancle() {
    this.outValueEmit.emit(false);
  }
  selectCall(event) {
    this.dialogDisplay = false;
    console.log(event,'eventshoudoa')
    if(event) {
      let datalist = event
      if (this.data) {
        this.inputValue = this.data.concat(datalist)
        return
      }else {
        this.list = this.list.concat(datalist);
        this.inputValue = this.list
      }
    }
  }
  preView () {
    if (this.data) {
      this.inValueDetail = this.data
      this.detaildisplay = true;
    }
  }
}
