import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { MarketingContentManageComponent } from './marketing-content-manage.component';
import { ContentAddComponent } from './content-add/content-add.component';
import { ContentDetailComponent } from './content-detail/content-detail.component';
export const routes: Routes = [
    {
        path: '',
        component: MarketingContentManageComponent,
        children: [
            {
                path: 'marketing-content-manage',
                component: MarketingContentManageComponent
            }
        ]
    },
    {
        path: 'content-detail',
        component: ContentDetailComponent
    }
]
export const MarketingContentManageRouting: ModuleWithProviders = RouterModule.forChild(routes);