import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'app-red-race',
  templateUrl: './red-race.component.html',
  styleUrls: ['./red-race.component.scss'],
  animations: [
    trigger('openHongbaoAnm', [
      state('inactive', style({ transform: 'rotateY(0)' })), // 无动作状态
      state('active', style({ transform: 'rotateY(360deg)' })), // 有动作状态(翻转360度)
      transition('inactive => active', animate('0.8s ease-out')) // 无动作到有动作的动画时间以及过渡方式
    ])
  ]
})
export class RedRaceComponent implements OnInit {
  @Input() inValueItem: any;
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
  msgs: Message[] = [];
  public loading = false;  // 数据加载loading
  public openState = 'inactive';  // 动画状态
  public showcontent = '0';
  public name = '恭喜发财, 大吉大利';
  public descName = '已存入您的积分账户中,可用于积分兑换';
  public isShowHongBao = true;
  constructor(
    private httpService: CusOperationHttpService
  ) { }

  ngOnInit() { 

  }
   // 开红包动画完成事件
   public openHongbaoAnmDone(e: { toState: any; }) {
    
    /**
     * 说明: 当前状态为inactive时, 若数据数据还处于loading, 则切换至有动画的active状态
     * active状态完成后切换回无状态inactive,这时候又会进入下一轮inactive判断
     * 视为无限播放动画, 直到有数据返回时, 即loading为true, 停止动画
     */
    // switch (e.toState) {
    //   case 'inactive':
    //       console.log('inactive')
    //     return this.openState = (this.loading) ? 'active' : 'inactive';
        
    //   case 'active':
    //       console.log('完成')
    //     return this.openState = 'inactive';
    // }
    if(e.toState == 'active'){
      // setTimeout(() => {
        this.loading = true;
        // this.loading = false;
        console.log('完成')
        let scoredata  = Math.round(Math.random()*1000 )
        this.name = scoredata + '积分';
      this.storescore(scoredata)
    }
  }
  storescore (scoredata){
    console.log('您的'+scoredata+'积分已存入您的积分账户')
    let params ={
      marketingCampaignId:'',
      modelId:'',
      scoredata:scoredata
    }
    this.httpService.taskUserRelationAdd(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    });
  
  }
  styleMethod01(){
    
    if(this.loading == false){
      console.log('styleMethod03')
      const style = {
        topcontent: true,
        topcontent2: false
      };
      return style;
    }else {
      const style = {
        topcontent: false,
        topcontent2: true
      };
      return style;
    }
  }

  // 开红包
  public openHongbao() {
    
    this.openState = 'active'  // 开始动画
    // 2秒后停止动画
   
  }

  showHongBao(){
    this.isShowHongBao = true;
  }
  closeClick(){
    this.isShowHongBao = false;
  }


}
